html {
  background: linear-gradient(180deg, #c4ebe2 0%, rgba(196, 235, 226, 0) 100%)
    fixed;
  background-attachment: fixed;
}

body {
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  position: absolute;
  width: 100vw;
  left: 0px;
  top: 0px;
}
